$blue: #0A2B4E;
$yellow: #FDB827;
$white: #fff;

.page-template-page-facility {
  .facilityIntroInner {
    text-align: center;
  }
  #facilityFeature {
    background: #f2f2f2;
    background-size: cover;
    background-position: top right;
    .facilityItemNavContainer {
      text-align: center;
      width: 800px;
      max-width: 100%;
      margin-left: auto;
      margin-right: auto;
      .facilityItemNav {
        display: inline-block;
        color: $blue;
        opacity: 0.4;
        transition: all .2s ease-in-out;
        margin-left: 15px;
        margin-right: 15px;
        text-transform: uppercase;
        font-weight: bold;
        &.slick-current, &:hover, &:focus { 
          opacity: 1; 
          text-decoration: none;
          outline: 0;
        }
        &:hover {
          cursor: pointer;
        }
      }
    }
    .facilityItemContainerSlider {
      .facilityItemContainer {
        background-color: #fff;
        padding: 30px;
        border-radius: 5px;
        box-shadow: none;
        margin-left: auto;
        margin-right: auto;
        transition: all 2s ease-in-out;
        margin-bottom: 40px;
        margin-top: 40px;
        max-width: 980px;
        .facilityItemImgContainer {
          position: relative;
          .facilityItemSuitable {
            position: absolute;
            z-index: 2;
            top: 0;
            left: 0;
            background: $yellow;
            border-bottom-right-radius: 5px;
            p {
              margin: 15px;
              color: $white;
              font-weight: bold;
            }
          }
          .facilityItemGallery {
            position: relative;
            .slick-arrow {
              position: absolute;
              z-index: 1;
              top: 43%;
              padding: 15px;
              background: rgba(0,0,0,0.4);
              color: #fff;
              opacity: 0.6;
              &:hover {
                opacity: 1;
                cursor: pointer;
              }
            }
            .slick-prev {
              left: 0;
            }
            .slick-next {
              right: 0;
            }
            .facilityItemGalleryImg {
              background-size: cover;
              background-position: center;
              height: 445px;
            }
          }
        }
        .amenitiesContainer {
          ul {
            margin-left: 0;
            li {
              font-size: 1rem;
              margin-bottom: 10px;
              i {
                margin-right: 10px;
                color: $yellow;
              }
            }
          }
        }
        .applyNowContainer {
          margin-top: 30px;
          .button {
            margin-right: 10px;
          }
        }
      }
      div.slick-current .facilityItemContainer {
        box-shadow: 0 0 45px -10px #999;
      }
    }
  }
  #addServices {
    h3 {
      font-size: 1.25rem;
      margin-top: 20px;
    }
    img {
      border-radius: 5px;
    }
    .addServicesSlider {
      .slick-arrow {
        font-size: 2rem;
        opacity: 0.2;
        &:hover {
          cursor: pointer;
          opacity: 1;
        }
      }
      .slick-prev {
        position: absolute;
        left: -40px;
        top: 40%;
      }
      .slick-next {
        position: absolute;
        right: -40px;
        top: 40%;
      }
      .addServicesItem {
        margin-left: 15px;
        margin-right: 15px;
      }
    }
  }
}